<template>
  <div class="bargain">
    <div class="head">
      <!-- <img class="objectfit-cover" src="./default-img/bargain/theme1/cover.png" alt="" /> -->
      <div class="swipter-box">
        <swiper :options="swiperOption" ref="mySwiper">
          <swiper-slide v-for="hotItem in hot" :key="hotItem.id">
            <router-link v-if="hotItem.cover" :to="{path: '/activity/detail/' + hotItem.id}" tag="img" class="objectfit-cover hotImg" :src="hotItem.cover" :alt="hotItem.alt"></router-link>
          </swiper-slide>
          <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
      </div>
      <p class="head-title">9800  873  690 </p>
      <ul class="head-sbt">
        <li></li>
        <li></li>
        <li></li>
      </ul>
    </div>
    <div class="join">
      <div class="boardline"></div>
      <div class="contain">
        <div class="join-form">
          <p class="title">989800 </p>
          <div class="kill-price">
            <div class="kill-price_person">
              <div class="kill-price_person-img">
                <img class="bargain-img" src="./default-img/killprice-person.jpg" alt="">
                <span class="bargain-nick"></span>
              </div>
              <span class="kill-price_person-name">vins</span>
            </div>
            <div class="kill-price_con">
              <div>
                <span class="aim-price">98</span></div>
              <div class="through-line">
                <span class="bold old-proce">988</span></div>
              <div>
                <span class="bold">0</span>
              </div>
              <div style="color: #999;">
                <span>1000</span>
                <span class="bold">123</span></div>
            </div>
          </div>
          <div class="act-join">
            <div class="act-join_person">
              <img src="./default-img/killprice-person.jpg" alt="" class="act-join_person-img">
              <span class="act-join_person-text"></span>
            </div>
            <div class="act-join_person">
              <img src="./default-img/killprice-person.jpg" alt="" class="act-join_person-img">
              <span class="act-join_person-text">lily</span>
            </div>
          </div>
        </div>
        <div class="splitline"></div>
        <div class="countdown-time">
          <div class="left-time">
            <span class="number">2</span>
            <span class="number">3</span>
            <span class="text"></span>
            <span class="number">0</span>
            <span class="number">8</span>
            <span class="text"></span>
            <span class="number">0</span>
            <span class="number">9</span>
            <span class="text"></span>
            <span class="number">2</span>
            <span class="number">9</span>
            <span class="text no-mr"></span>
          </div>
        </div>
        <div class="join-btn">
          <button type="button"></button>
        </div>
        <div class="sawtoothline"></div>
      </div>
    </div>
    <p class="m-card-title"></p>
    <div class="m-card m-card_shadow pro-des">
      <div class="m-card-con">
        <img class="img" src="./default-img/placeholder-activity1.jpg" alt="" />
        <p class="t"></p>
        <p class="text">96.8%
          <p class="text"></p>
          <p class="text">
            <span>0591-85533309</span>
          </p>
          <p class="text">
            <span></span>
          </p>
      </div>
    </div>
    <p class="m-card-title"></p>
    <div class="m-card m-card_shadow port-rule">
      <div class="m-card-con">
        1.<br /> 2.
        <br /> 3.
        <br /> 4.
      </div>
    </div>
    <p class="m-card-title"></p>
    <div class="m-card port-manger">
      <div class="m-card-con">
        <span class="tag-auth"></span>
        <img class="img" src="./default-img/placeholder-activity1.jpg" alt="" />
        <p class="text">
          96.8%""
        </p>
        <div class="contact">
          <div class="logo">
            <img src="./default-img/placeholder-activity1.jpg" alt="" />
            <p></p>
          </div>
          <div class="code-img">
            <div class="phone-box">
              <p class="t"></p>
              <span class="phone">
                <i class="el-icon-phone icon icon-left"></i>
                <a href="tel:0759-85533306">0759-85533306</a>
              </span>
              <p class="adr">182()</p>
            </div>
            <img v-if="true" class="port-qrcode" src="./default-img/placeholder-activity1.jpg" alt="" />
          </div>
        </div>
      </div>
    </div>
    <div class="m-card m-card_shadow">
      <div class="m-card-con port-stores">
        <div class="port-stores-list">
          <div class="port-stores-list_lt">
            <span class="stores-dotted"></span>
          </div>
          <div class="port-stores-list_bd">
            <p class="stores-name">1</p>
            <p class="stores-addr">182()</p>
            <p class="stores-own">
              <span></span>
              <span>18210666666</span>
            </p>
          </div>
          <div class="port-stores-list_rt">
            <a class="fast-call" href="tel:18210666666"></a>
          </div>
        </div>
        <div class="port-stores-list">
          <div class="port-stores-list_lt">
            <span class="stores-dotted"></span>
          </div>
          <div class="port-stores-list_bd">
            <p class="stores-name">1</p>
            <p class="stores-addr">182()</p>
            <p class="stores-own">
              <span></span>
              <span>18210666666</span>
            </p>
          </div>
          <div class="port-stores-list_rt">
            <a class="fast-call" href="tel:18210666666"></a>
          </div>
        </div>
      </div>
    </div>
    <p class="m-card-title"></p>
    <div class="m-card m-card_shadow rank">
      <div class="m-card-con">
        <div class="rank-list" v-for="(item, index) in rank" :key="item.finalPrice">
          <img v-if="index < 3" class="rank-list_babel" :src="getRankImg(index)" alt="">
          <span v-else class="rank-list_babel">{{index + 1}}</span>
          <img class="rank-list_personImg" :src="item.person.src" alt="">
          <div class="rank-list_person">
            <p class="person-name">{{item.person.name}}</p>
            <p class="bargain-randomName">{{rankRandom[index] || rankRandom[rankRandom.length - 1]}}</p>
          </div>
          <div class="rank-list_bargain">
            <span class="final-price">{{item.finalPrice}}</span></div>
        </div>
      </div>
    </div>
    <p class="possession"></p>
  </div>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'

// 
import defaultPerson from './default-img/killprice-person.jpg'
import defaultBanner from './default-img/bargain/theme1/cover.png'

export default {
  name: 'PreviewBargain',
  data () {
    return {
      swiperOption: {
        autoplay: true,
        pagination: {
          el: '.swiper-pagination'
        }
      },
      hot: [
        {
          id: 1,
          cover: defaultBanner,
          alt: 'banner1'
        },
        {
          id: 2,
          cover: defaultBanner,
          alt: 'banner2'
        }
      ],
      joinForm: {
        userName: '',
        phone: '',
        vcode: ''
      },
      rank: [
        {
          person: {
            id: 1,
            src: defaultPerson,
            name: 'RuBy'
          },
          finalPrice: 98
        },
        {
          person: {
            id: 2,
            src: defaultPerson,
            name: 'vins'
          },
          finalPrice: 458
        },
        {
          person: {
            id: 3,
            src: defaultPerson,
            name: 'lily'
          },
          finalPrice: 1235
        },
        {
          person: {
            id: 4,
            src: defaultPerson,
            name: 'comty'
          },
          finalPrice: 9869
        }
      ],
      rankRandom: [
        '',
        '',
        '',
        ''
      ]
    }
  },
  computed: {
    swiper () {
      return this.$refs.mySwiper.swiper
    }
  },
  components: {
    swiper,
    swiperSlide
  },
  methods: {
    getRankImg (index) {
      return require(`./default-img/rank${index + 1}@2x.png`)
    }
  }
}
</script>

<style lang="css" src="./comment.css" scoped></style>
<style lang="scss" src="./bargain.scss" scoped></style>
<style lang="scss" scoped>
.bargain-page-header {
  color: #fff;
  background: #000;
}
</style>




